/**
 * @description  controllers
 */

module.exports = window.angular.module('controllers', [])

// @ngInject
.controller('AppCtrl', function($scope) {})

// @ngInject
.controller('HomeCtrl', function($rootScope, $scope, $timeout, foPopover) {

  $scope.open = function() {
    debugger;
    foPopover.open({
      target: document.querySelector('#open'),
      template: 'open.html',
      position: 'bottom center',
      scope: $scope
    });
  };

  $scope.open2 = function() {
    foPopover.open({
      target: document.querySelector('#delete2'),
      template: 'delete2.heml',
      position: 'top center'
    });
  };

  $scope.yes = function () {
    console.log(1111111);
    foPopover.close();
  };

  $scope.types = [
    // {
    //   name: 'Top Left',
    //   position: 'top left'
    //  },
    {
      name: 'Top Center',
      position: 'top center'
    },
    {
      name: 'Top Right',
      position: 'top right'
    },


    {
      name: 'Right Top',
      position: 'right top'
    },
    {
      name: 'Right Center',
      position: 'right center'
    },
    {
      name: 'Right Bottom',
      position: 'right bottom'
    },

    {
      name: 'Bottom Left',
      position: 'bottom left'
    },
    {
      name: 'Bottom Center',
      position: 'bottom center'
    },
    {
      name: 'Bottom Right',
      position: 'bottom right'
    },


    {
      name: 'Left Top',
      position: 'left top'
    },
    {
      name: 'Left Center',
      position: 'left center'
    },
    {
      name: 'Left Bottom',
      position: 'left bottom'
    },

    {
      name: 'Top Left Diagonal',
      position: 'top left diagonal'
    },
    {
      name: 'Top Right Diagonal',
      position: 'top right diagonal'
    },
    {
      name: 'Bottom Left Diagonal',
      position: 'bottom left diagonal'
    },
    {
      name: 'Bottom Right Diagonal',
      position: 'bottom right diagonal'
    }
  ];

  $timeout(function() {
    new Select({
      el: document.querySelector('.drop-select'),
      className: 'select-theme-default'
    });
  }, 2);

  $scope.select = function() {
    console.log($scope.type);
  }

});



